import styled from "styled-components";

export const Container = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: auto;
 
`;

export const ContainerItem = styled.div`
height: auto;
  padding:5vw;
  width: 80%;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  margin: 0px;

`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
`;

export const Video = styled.div`
  position: relative;
  display: flex;
  justify-content:center;
  align-items:center;
  width: 80%;
  height: 75vh;

`;
export const Form = styled.form`
 width: 70%;
 height: auto;
 display:flex;
 flex-direction:column;
 justify-content:center;
 align-items:center;
`;
export const InputGroup = styled.div`
 width: 100%;
 height: 4vh;
 display:flex;
 flex-direction:row;
 justify-content:space-between;
 align-items:center;
padding: 0px;
color:black;


`;

export const Inpute = styled.input`
 margin-right: ${(props) => props.right || 0} !important;
  margin-left: ${(props) => props.left || 0} !important;
  margin-top: ${(props) => props.top || 0} !important;
  margin-bottom: ${(props) => props.bottom || 0} !important;
width: 100%;
height: 4vh;
padding: 10px;
background-color:white ;
border-radius:0px;
color:black;

::placeholder{
color:rgb(107, 106, 106);
}

`; export const TexteArea = styled.textarea`
 margin-right: ${(props) => props.right || 0} !important;
  margin-left: ${(props) => props.left || 0} !important;
  margin-top: ${(props) => props.top || 0} !important;
  margin-bottom: ${(props) => props.bottom || 0} !important;
width: 100%;
height: 10vh;
padding: 10px;
background-color:white ;
border-radius:0px;

::placeholder{
color:rgb(107, 106, 106);
}

`;



export const DivFrame = styled.div`
 position: relative;
  width: 80%;
  height: 75vh;
 
`;