import React, { useState, useEffect } from "react";

import * as S from "./styles";

import ListaHok from "../../dados/hooks/listaHok";
import StripeContainer from "../../components/Stripe/StripeContainer";
import { cpfMask } from "../../utils/utils.js";
import Layout from "../../layout";
import logoPW from "../../assets/logo-pw.png";
import {useHistory} from "react-router-dom"

function Pagamento() {
  const listaHok = ListaHok();
  const espetaculoUsuario = listaHok.listaEspetaculosDoUsuario;
  const plano = listaHok.listaPlanos;
  const [temItem, setTemItem] = useState(false);
  const [tipo, setTipo] = useState("");
  const [compraSuccess, setCompraSuccess] = useState(false)
  // console.log(tipo)
  // const [documentId, setDocumentId] = useState("");
  const history = useHistory();
  const [cpf, setCpf] = useState("");
  const [nome, setNome] = useState("");
  function handleChangeNome(e) {
    setNome(e.target.value);
  }

  function handleChangeCpf(e) {
    const cpfMasked = cpfMask(e.target.value);

    setCpf(cpfMasked);
  }

  useEffect(() => {
    if (espetaculoUsuario.key) {
      setTemItem(true);
      setTipo("peca");
    }
    if (plano.id) {
      setTemItem(true);
      setTipo("assinatura");
      // console.log(plano)
    }
  }, [espetaculoUsuario.key, plano]);

  return (
    <Layout>
      <center>

        <S.Container paymentTrue={compraSuccess}>
          {!temItem && (
            <>
              <S.TelaVazia>
                <i style={{fontSize:'3rem'}} className="bi bi-cart-x-fill"></i>
                Encontre um de Nossos Espet√°culos e adicione ao carrinho :D
              </S.TelaVazia>
            </>
          )}
          {temItem && espetaculoUsuario?.key && tipo === "peca" &&(
            <>
              <S.Span size="5">Pagamento</S.Span> <br />
              <S.Div>
                <S.Img
                  style={{ float: "left" }}
                  src={espetaculoUsuario.thumbnail}
                ></S.Img>
                <div style={{ float: "right" }}>
                  <h1> {espetaculoUsuario?.tituloEsp}</h1>
                  <h1>
                    {" "}
                    R$:{" "}
                    {espetaculoUsuario?.valorPreco
                      ? espetaculoUsuario?.valorPreco
                      : listaHok?.listaPlanos}
                  </h1>
                </div>
              </S.Div>
              <center>
                {/* {console.log(espetaculoUsuario)} */}
                <S.InputCardNumber
                  placeholder="CPF"
                  required
                  value={cpf}
                  onChange={handleChangeCpf}
                />
                <S.InputCardNumber
                  placeholder="Nome"
                  required
                  value={nome}
                  onChange={handleChangeNome}
                />
              </center>
              <StripeContainer type="peca"  amount={espetaculoUsuario?.valorPreco.replace(",", "")} nome={nome} cpf={cpf} />
              
            </>
          )}
          {temItem && plano && tipo === "assinatura" &&(
            <>
              <S.Span size="5">Pagamento</S.Span> <br />
              <S.Div>
                <S.ImgPlano
                  style={{ float: "left" }}
                  src={logoPW}
                ></S.ImgPlano>
                <div style={{ float: "right" }}>
                  <h1>Plano {plano?.name}</h1>
                  <h1>
                    {" "}
                    R$:{" "}
                    {plano?.value.replace("$", "")}
                  </h1>
                </div>
              </S.Div>
              <center>
                 <S.InputCardNumber
                  placeholder="CPF"
                  required
                  value={cpf}
                  onChange={handleChangeCpf}
                />
                <S.InputCardNumber
                  placeholder="Nome"
                  required
                  value={nome}
                  onChange={handleChangeNome}
                />
              </center>
              <StripeContainer setCompraSuccess={setCompraSuccess} type="assinatura" amount={plano?.value?.replace(",", "").replace("$", "").replace(".", "")} nome={nome} cpf={cpf} />
            </>
          )}
          
        </S.Container>
      </center>
    </Layout>
  );
}

export default Pagamento;
