import React, { useState, useEffect } from "react";
import * as S from "./styles";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";
import firebase from "firebase";
import { useParams } from "react-router-dom";


function ConfirmCompra() {
  const history = useHistory();
  const [compradosArrayState, setCompradosArrayState] = useState()
  const [assinaturaArrayState, setAssinaturaArrayState] = useState()
  const params = useParams("/ConfirmCompra/:type");
  const { type } = params;

  useEffect(() => {
    if (type === "peca") {
      const compradosArray = firebase
        .database()
        .ref("lancamentoVenda/" + firebase.auth().currentUser.uid);
      // async function firebird(){}
      compradosArray.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          const data = snapshot.val();
          setCompradosArrayState(data);
          // console.log(data);
        }
      });
    } else {
      const assinaturaArray = firebase
        .database()
        .ref("assinaturas/" + firebase.auth().currentUser.uid);
      // async function firebird(){}
      assinaturaArray.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          const data = snapshot.val();
          setAssinaturaArrayState(data);
          // console.log(data);
        }
      });
    }
  }, [type]);

  return (
    <>
      <Header />

      <S.Container>
          <S.Span className="p-5">
            Agradecemos sua compra
          </S.Span>
            {!assinaturaArrayState && type !== "assinatura" ? (
              <S.Span className="p-2" cor="#E4B810">
                Voc√™ tem 24h para assistir esse espet√°culo.
              </S.Span>
              ) : (<div></div>)
            }
            
         
        <div style={{display: "flex", flexDirection: "column", alignItems:'center'}}>
        {compradosArrayState && type === "peca" ? (
          <>
            
              <S.Img src={compradosArrayState[0]?.pe√ßa.thumbnail}></S.Img>
           

            <S.Span className="pt-2" cor="#E4B810">
              {compradosArrayState[0]?.valorDia}
            </S.Span>
            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {compradosArrayState[0]?.data.split('/')[1].concat('/'+compradosArrayState[0]?.data.split('/')[0]).concat('/'+compradosArrayState[0]?.data.split('/')[2])}
              {/* /{" "} */}
              {/* {compradosArrayState[0]?.data?.mes} /{" "}
              {compradosArrayState[0]?.data?.ano} as{" "}
              {compradosArrayState[0]?.data?.horas} Horas */}
            </S.Span>
            {/* <S.Span cor="#E4B810">
              Expira na data:{" "}
              {parseInt(compradosArrayState[0]?.data) + 1} /{" "}
              {compradosArrayState[0]?.data?.mes} /{" "}
              {compradosArrayState[0]?.data?.ano} as{" "}
              {compradosArrayState[0]?.data?.horas} Horas
            </S.Span> */}

            <S.Span cor="#E4B810">
              Total R$: {compradosArrayState[0]?.valor}
            </S.Span>
          </>
        ) :
        assinaturaArrayState && type === "assinatura" ?
        (
          <>
            
            {/* <S.Img src={compradosArrayState[0]?.pe√ßa.thumbnail}></S.Img> */}
           
            <S.Span className="pt-2" cor="#E4B810">
              Plano {assinaturaArrayState[0]?.nameAssinatura}
            </S.Span>
            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {assinaturaArrayState[0]?.data.split('/')[1].concat('/'+assinaturaArrayState[0]?.data.split('/')[0]).concat('/'+assinaturaArrayState[0]?.data.split('/')[2])}
              {/* /{" "} */}
              {/* {compradosArrayState[0]?.data?.mes} /{" "}
              {compradosArrayState[0]?.data?.ano} as{" "}
              {compradosArrayState[0]?.data?.horas} Horas */}
            </S.Span>
            {/* <S.Span cor="#E4B810">
              Expira na data:{" "}
              {parseInt(compradosArrayState[0]?.data) + 1} /{" "}
              {compradosArrayState[0]?.data?.mes} /{" "}
              {compradosArrayState[0]?.data?.ano} as{" "}
              {compradosArrayState[0]?.data?.horas} Horas
            </S.Span> */}

            <S.Span cor="#E4B810">
              Total R$: {assinaturaArrayState[0]?.valor}
            </S.Span>
          </>
        ) :
        (
          <div></div>
        )
        }
        </div>

        <S.AreaButtons>
          <S.Button className="btn" onClick={() => history.push(`/`)}>
            <S.Span cor="#FFF">
              {" "}
              <i className="bi bi-chevron-left"></i>Voltar
            </S.Span>
          </S.Button>
          <S.ButtonContinuar
            className="btn"
            onClick={() => history.push(`/espetaculos`)}
          >
            Meus espet√°culos
          </S.ButtonContinuar>
        </S.AreaButtons>
      </S.Container>
      <Footer />
    </>
  );
}
export default ConfirmCompra;
