import React, { useContext, useEffect, useState } from "react";
import * as S from "./styles";
import { SwiperSlide } from "swiper/react";
import HorizontalList from "../../components/HorizontalList";
import Card from "../../components/Card";
import Section from "../../components/Section";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";
import ListaTeste from "./lista";
import firebase from "firebase";
import axios from "axios";
import { AuthContext } from "../../auth/AuthContext";
import * as SCard from "../Canais/styles";

function Espetaculos() {
  const { currentUser } = useContext(AuthContext);
  const [compradosArrayState, setCompradosArrayState] = useState([])
  const [favArrayState, setfavArrayState] = useState(null)
  const [favArrayStateCanal, setfavArrayStateCanal] = useState(null)

  const api = axios.create({
    baseURL: "https://json-serverepw.herokuapp.com/user",
  });

  useEffect(() => {

    const compradosArray = firebase.database().ref('lancamentoVenda/' + firebase.auth().currentUser.uid)
    const favadosArray = firebase.database().ref('listaFav/' + firebase.auth().currentUser.uid)
    const favadosCanalArray = firebase.database().ref('listaFavcanal/' + firebase.auth().currentUser.uid)

    compradosArray.on('value', (snapshot) => {
      if (snapshot.val() !== null) {
        console.log(snapshot.val())
        const compradosToPush = []
        snapshot.val().forEach(element => {
          const dataCompra = new Date(element.data)
          const today = new Date()
          const dataExpiracao = dataCompra.getDate() + 1
          const horaExpiracao = dataCompra.getHours()
          if (today.getDate() <= dataExpiracao && today.getHours() <= horaExpiracao) {
            console.log("Pe√ßa dentro da condi√ß√£o", element)
            compradosToPush.push(element)
          } 
        });
        setCompradosArrayState(compradosToPush)
      }
    })

    favadosArray.on('value', (snapshot) => {
      if (snapshot.val() !== null) {
        setfavArrayState(snapshot.val())
      }
    })

    favadosCanalArray.on('value', (snapshot) => {
      if (snapshot.val() !== null) {
        setfavArrayStateCanal(snapshot?.val())
      }
    })
  }, [])

  const history = useHistory();
  useEffect(() => {
    if (currentUser) {
      async function fav() {
        // const { data } = await api.get("/" + userId);
        // if (data.fav !== undefined) {
        //   localStorage.setItem("fav", JSON.stringify(data.fav));
        // } else {
        //   localStorage.setItem("fav", []);
        // }

        // if (data.assistidos !== undefined) {
        //   localStorage.setItem("assistidos", JSON.stringify(data.assistidos));
        // } else {
        //   localStorage.setItem("assistidos", []);
        // }
        if (compradosArrayState !== null) {
          localStorage.setItem(
            "comprados",
            JSON.stringify(compradosArrayState)
          );
        } else {
          localStorage.setItem("comprados", []);
        }
        // if (data.favCanais !== undefined) {
        //   localStorage.setItem(
        //     "canaisFavoritos",
        //     JSON.stringify(data.favCanais)
        //   );
        // } else {
        //   localStorage.setItem("canaisFavoritos", []);
        // }
      }
      fav();
    } else {
      history.push("/login");
    }
  }, [api, currentUser, history, compradosArrayState]);

  function PaginaAssitir(dia, mes, ano, horas, videoID, videoID2, key) {
    history.push(`/playespetaculo/${videoID}/${videoID2}/${key}`);
  }

  async function desFavoritar(peca) {
    const userId = await firebase.auth().currentUser?.uid;
    const array1 = favArrayState.filter((e) => {
      return e.peca?.key !== peca
    })
    setfavArrayState(array1)
    firebase.database().ref('listaFav/' + userId).set(array1)
  }

  return (
    <>
      <Header />

      <S.Container>
        <h2>Meus Espet√°culos</h2>
        <Section title="Adquiridos" filter>
          <HorizontalList id="emAlta">
            {compradosArrayState ? compradosArrayState?.map((card, index) => {

              return (
                <SwiperSlide key={String(index)}>
                  <S.CardTest>
                    <S.Img
                      src={card?.pe√ßa.thumbnail}
                      style={{
                        position: "absolute",
                        width: 250,
                        height: 400,
                      }}
                      value={card?.pe√ßa.key}
                    />

                    <S.InfoCard>
                      <S.InfoCardConteudo>
                        <S.Classificacao>
                          {card?.pe√ßa.classificacao}
                        </S.Classificacao>
                        <S.Coracao>
                          <i className="bi bi-suit-heart-fill"></i>
                        </S.Coracao>

                        <S.Compartilhar>
                          <i className="bi bi-share-fill"></i>
                        </S.Compartilhar>
                      </S.InfoCardConteudo>

                      <S.Titulo>{card?.pe√ßa.tituloEsp} </S.Titulo>

                      <S.ConteudoCard>Com {card?.pe√ßa.artista}</S.ConteudoCard>


                      <S.ConteudoCard>{card?.pe√ßa.categoria}</S.ConteudoCard>
                      <S.Sinopsia style={{ overflow: "hidden" }}>
                        {card?.pe√ßa.sinopsiaEsp}
                      </S.Sinopsia>

                      <S.ButtonAcessar
                        onClick={() => {
                          PaginaAssitir(
                            card?.pe√ßa.valorHorarioSe?.dia,
                            card?.pe√ßa.valorHorarioSe?.mes,
                            card?.pe√ßa.valorHorarioSe?.ano,
                            card?.pe√ßa.valorHorarioSe?.horas,
                            card?.pe√ßa.videoID,
                            card?.pe√ßa.videoID2,
                            card?.pe√ßa.key
                          );
                        }}
                        className="btn btn-warning"
                        style={{ color: "#000" }}
                      >
                        {" "}
                        Assistir{" "}
                      </S.ButtonAcessar>
                    </S.InfoCard>
                  </S.CardTest>

                </SwiperSlide>
              )

            }) :
              ListaTeste?.map((item, index) => {
                return (
                  <SwiperSlide key={String(index)}>
                    <Card noContent />
                  </SwiperSlide>
                )
              })

            }

          </HorizontalList>
        </Section>
        <Section title="Favoritos" filter>
          <HorizontalList id="emAlta">
            {favArrayState ? favArrayState?.map((card, index) => {
              return (

                <SwiperSlide key={index}>
                  <S.CardTest>
                    <S.Img
                      src={card?.peca?.thumbnail}
                      style={{
                        position: "absolute",
                        width: 250,
                        height: 400,
                      }}
                      value={card?.peca?.key}
                    />

                    <S.InfoCard>
                      <S.InfoCardConteudo>
                        <S.Classificacao>
                          {card?.peca?.classificacao}
                        </S.Classificacao>
                        <S.Coracao onClick={() => desFavoritar(card?.peca?.key)}>
                          <i className="bi bi-suit-heart-fill"></i>
                        </S.Coracao>

                        <S.Compartilhar>
                          <i className="bi bi-share-fill"></i>
                        </S.Compartilhar>
                      </S.InfoCardConteudo>

                      <S.Titulo>{card?.peca?.title}</S.Titulo>
                      <S.ConteudoCard>Com {card?.peca?.Artista}</S.ConteudoCard>
                      {/* <S.ConteudoCard>
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>
                          <i className="bi bi-star-fill"></i>
                        </S.ConteudoCard> */}
                      <S.ConteudoCard>{card?.peca?.categoria}</S.ConteudoCard>
                      <S.Sinopsia style={{ overflow: "hidden" }}>
                        {card?.peca?.synopsis}
                      </S.Sinopsia>

                      <S.ButtonAcessar
                        className="btn btn-warning"
                        style={{ color: 'black' }}
                        onClick={() => {
                          history.push(`/cardezao/${card?.peca?.key}`);
                           
                        }}
                      >
                        Saiba Mais
                      </S.ButtonAcessar>
                    </S.InfoCard>
                  </S.CardTest>

                </SwiperSlide>
              )
            }) : ListaTeste?.map((item, index) => {
              return (
                <SwiperSlide key={String(index)}>
                  <Card noContent />
                </SwiperSlide>
              )
            })}
          </HorizontalList>
        </Section>

        <Section title="Assistidos" filter>
          <HorizontalList id="emAlta">
            {ListaTeste?.map((card, index) => {
              if (!localStorage.getItem("assistidos"))
                return (
                  <SwiperSlide key={String(index)}>
                    <Card noContent />
                  </SwiperSlide>
                );
              return (
                <SwiperSlide key={String(index)}>
                  {!localStorage.getItem("assistidos").includes(card?.title) ? (
                    <>
                      <Card noContent />
                      {/* <S.A onClick={() => validacaoHorario(card)} > Assistir</S.A> */}
                    </>
                  ) : (
                    <S.CardTest>
                      <S.Img
                        src={card?.thumbnail}
                        style={{
                          position: "absolute",
                          width: 250,
                          height: 400,
                        }}
                        value={card?.key}
                      />

                      <S.InfoCard>
                        <S.InfoCardConteudo>
                          <S.Classificacao>
                            {card?.classificacao}
                          </S.Classificacao>
                          <S.Coracao>
                            <i className="bi bi-suit-heart-fill"></i>
                          </S.Coracao>

                          <S.Compartilhar>
                            <i className="bi bi-share-fill"></i>
                          </S.Compartilhar>
                        </S.InfoCardConteudo>

                        <S.Titulo>{card?.tituloEsp} </S.Titulo>
                        <S.ConteudoCard>Com {card?.artista}</S.ConteudoCard>
                        {/* <S.ConteudoCard>
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>
                          <i className="bi bi-star-fill"></i>
                        </S.ConteudoCard> */}
                        <S.ConteudoCard>{card?.categoria}</S.ConteudoCard>
                        <S.Sinopsia style={{ overflow: "hidden" }}>
                          {card?.sinopsiaEsp}
                        </S.Sinopsia>

                        <S.ButtonAcessar
                          onClick={() => {
                            PaginaAssitir(
                              card?.valorHorarioSe?.dia,
                              card?.valorHorarioSe?.mes,
                              card?.valorHorarioSe?.ano,
                              card?.valorHorarioSe?.horas,
                              card?.videoID,
                              card?.videoID2,
                              card?.key
                            );
                          }}
                          className="btn btn-warning"
                        >
                          {" "}
                          Assistir{" "}
                        </S.ButtonAcessar>
                      </S.InfoCard>
                    </S.CardTest>
                  )}
                </SwiperSlide>
              );
            })}
          </HorizontalList>
        </Section>
        <Section title="Canais Favoritos" filter>
          <HorizontalList id="emAlta">
            {favArrayStateCanal ? favArrayStateCanal?.map((card, index) => {

              return (
                <SwiperSlide key={index}>

                  <SCard.CardCanais >
                    <SCard.Img
                      src={card?.canal?.thumbnail}
                      style={{
                        position: "absolute",
                        width: 250,
                        height: 250,
                        borderRadius: "50%",
                      }}
                      value={card?.canal?.key}
                      alt="imagem referente ao canal"
                    />
                    <SCard.InfoCard>
                      <SCard.Span
                        size="18"
                        style={{ marginBottom: "15px" }}
                      >
                        <SCard.CoracaoFav  >
                          <i className="bi bi-suit-heart"></i>
                        </SCard.CoracaoFav>
                      </SCard.Span>
                      <SCard.Span size="20">{card?.canal?.nome}</SCard.Span>

                      <SCard.Span size="10">
                        <i className="bi bi-circle-fill"></i>{" "}
                        <SCard.Span size="15">
                          {card?.canal?.categoria}
                        </SCard.Span>{" "}
                      </SCard.Span>
                      <SCard.Span></SCard.Span>
                      <SCard.verMais
                        size="18"
                        style={{ marginTop: "15px" }}
                        onClick={() => {
                          history.push(`/FichaCanal/${card?.canal?.key}`);
                           
                        }}
                      >
                        Ver mais
                      </SCard.verMais>
                    </SCard.InfoCard>
                  </SCard.CardCanais>

                </SwiperSlide>
              )
            }) : ListaTeste?.map((item, index) => {
              return (
                <SwiperSlide key={String(index)}>
                  <Card noContent />
                </SwiperSlide>
              )
            })}
          </HorizontalList>
        </Section>
      </S.Container>
      <Footer />
    </>
  );
}

export default Espetaculos;
