import styled from "styled-components";


export const div = styled.div`
display: flex;
width: 100%;
height: 80px;
z-index: 1;
align-items: center;
justify-content: space-around;
background-color: transparent;
opacity: 0;
:hover{
 opacity: 1;
}

`


export const Span = styled.span`
    color: white;
    text-align: justify;
    font-size:${props => props.size + "px"};
    font-weight:bold;
    :hover{
    color: ${props => props.hover};
    }
`


export const Estrelinhas = styled.span`
cursor: pointer;
color: ${(props) => props.seleacionado+'!important ' || 'white'}  ;
color: ${(props) => props.seleacionadoHover || 'white'}  ;
font-size:20px;
:hover{
width: 70px;
color:#fdcc12;
}

`

