function httpGet() {
  const xmlHttp = new XMLHttpRequest();
  xmlHttp.open("GET", "https://json-serverepw.herokuapp.com/lista/2", false);
  xmlHttp.send();
  var parse = JSON.parse(xmlHttp.responseText)
  console.log(parse)
  parse.valor = "0,00";
  return parse;
}

export default httpGet();
