import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import app from "../../auth/config";
import Layout from "../../layout";
import "./style.css";
import * as S from "./styles";
import firebase from "firebase"
import IMG from '../../assets/imagemAssinatura.png'
import ListaHok from "../../dados/hooks/listaHok";


import ImgMensal from '../../assets/mensal.jpeg'
import ImgTrimestal from '../../assets/trimestral.jpeg'
import ImgAnual from '../../assets/anual.jpeg'

function Assinatura() {
  const history = useHistory();
  const listaHok = ListaHok();

  // const { updateSignature, userIsAuthenticated, signature } = useAuth();
  // const { payFree } = usePayments();
  const userIsAuthenticated = app.auth().currentUser;
  // const isSignature = app.auth().currentUser && localStorage.getItem("signature");
  // const { addToPayment } = usePayments();
  const [plans, setPlans] = useState([]);
  // function formatcurrency(number) {
  //   let str = number.split("$")[1];
  //   str = str.split(".");
  //   return str;
  // }

  
  function proximo() {
    history.push(`/LoginCart/id`);
  }

  useEffect(() => {
    async function getPlan() {
      try {
        const { data } = await axios.get(
          "https://json-serverepw.herokuapp.com/plains"
        );
        setPlans(data);
      } catch (error) {
      }
    }

    // if(userIsAuthenticated) {
    //   const userId = firebase.auth().currentUser.uid;

    //   let email = firebase.database().ref('users/' + firebase.auth().currentUser.uid + "/signature");
    //   email.on('value', (snapshot) => {
    //     if (snapshot.val() !== null) {
    //     }
    //   })

    // }

    getPlan();
  }, []);

  function payment(plano, isTesteGratis) {
    // firebase -> dados usuario -> data/hora  ## salvar
    const userId = firebase.auth().currentUser.uid;
    const data = new Date().toLocaleString("en-US", { timeZone: 'America/Sao_Paulo' })

    if (isTesteGratis) {
      // firebase.database().ref('users/' + userId).update({
      //   signature: true
      // })
      firebase.database().ref('assinaturas/' + userId).update({
        userId, data, id: plano.id, name: plano.name, value: plano.value
      })
      firebase.database().ref('users/' + userId).update({
        signature: true
      })
      history.push(plano.paymentLink)
    } 
    // else {
    //   // firebase.database().ref('users/' + userId).update({
    //   //   signature: true
    //   // })
    //   firebase.database().ref('assinaturas/' + userId).update({
    //     userId, data, id: plano.id, name: plano.name, value: plano.value
    //   })
    //   window.location.href = plano.paymentLink;
    // }

  }



  return (
    <Layout>

      <S.Wrapper>
        <S.Container>
          <>
            <S.Header>
              <S.Div>
                <span>Teste gr√°tis por 7 dias</span>

                <S.HeaderSpan>
                  Aproveite desconto em lan√ßamentos e o nosso cat√°logo com
                  conte√∫do exclusivo e ilimitado em pe√ßas teatrais, stand-ups,
                  shows e palestras.
                </S.HeaderSpan>

                <S.ButtonPagar
                  onClick={() => {
                    userIsAuthenticated
                      ? (payment({
                          "id": "123",
                          "name": "Gratis",
                          "value": "$0.00",
                          "isActive": true,
                          "is_test_period": true,
                          "period": 7,
                          "paymentLink": "/espetaculos/assinante"
                      }, true))
                      : history.push("/LoginAssinatura");
                  }}
                >
                  INICIAR PER√çODO DE TESTE
                </S.ButtonPagar>
              </S.Div>
              <S.Im>
                <img className="img" alt="" src={IMG}></img>
              </S.Im>
            </S.Header>

            <S.HeaderSpan>Escolha o melhor plano para voc√™:</S.HeaderSpan>

            <S.CartActions>
              <S.ButtonVoltar onClick={() => history.push(`/`)}>
                <i className="bi bi-chevron-left"></i>Voltar{" "}
              </S.ButtonVoltar>
              <div className="divi-2">
                {plans?.map((plano) => {


                  return (
                    <>
                      {plano.name === "Mensal" &&

                        <>
                          <span onClick={() => {
                            proximo()
                            listaHok.AdicionarPlanos(plano)
                          }

                          } > <img alt="" className="imgPlano" src={ImgMensal} /></span>
                        </>
                      }
                      {plano.name === "Trimestral" &&

                        <span onClick={() => {
                          listaHok.AdicionarPlanos(plano)
                          proximo()
                        }} ><img alt="" className="imgPlano" src={ImgTrimestal} /></span>

                      }
                      {
                        plano.name === "Anual" &&
                        <span href={plano.paymentLink} onClick={() => {
                          proximo()
                          listaHok.AdicionarPlanos(plano)
                        }} ><img alt="" className="imgPlano ano" src={ImgAnual} /></span>

                      }

                    </>
                  );
                })}
              </div>
            </S.CartActions>
          </>
          <S.CartInfo>
            <span>
              Torne-se um assinante e garanta 50% de desconto neste conte√∫do
            </span>
            <S.CartInfoText>
              E ainda tenha acesso a um amplo conte√∫do em pe√ßas teatrais,
              shows musicais e palestras para assistir a qualquer hora(exceto
              lan√ßamentos).
            </S.CartInfoText>
          </S.CartInfo>
        </S.Container>
      </S.Wrapper>

    </Layout >
  );
}

export default Assinatura;
