import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import axios from "axios";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./style.css";
import useDadosList from "../../dados/hooks/listaHok";
import firebase from "firebase";
import ListaHook from "../../dados/hooks/listaHok";
import styled from "styled-components";

const OverlayConfirmCompra = styled.div`
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba($color: #000, $alpha: 0.2);
  display: flex;
  justify-content: center;
  align-items: center;

`;

const ModalConfirmCompra = styled.div`
  background: #d7ac02;
  font-weight: 500;
  width: 30vw;
  min-height: 40vh;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 20px;       
  color: black;

  @media (max-width:768px) {
    width: 90vw !important;	     
  }
  
  button {
    background-color:#222;
    font-size: 3vh;
    color: #fff;
    border-radius: 8px;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    &:hover{
      background: #363636;
    }
  
  } 
`;


const CARD_OPTIONS = {
  iconStyle: "solid",
  hidePostalCode: true,
  style: {
    base: {
      color: "#303238",
      fontSize: "16px",
      fontFamily: '"Open Sans", sans-serif',
      fontSmoothing: "antialiased",
      "::placeholder": {
        color: "#CFD7DF",
      },
    },
    invalid: {
      color: "#e5424d",
      ":focus": {
        color: "#303238",
      },
    },
  },
};

export default function PaymentForm(props) {
  const [nameComprador, setNameComprador] = useState("");
  const [compradosArrayState, setCompradosArrayState] = useState(null);
  const [assinaturaArrayState, setAssinaturaArrayState] = useState(null)

  const [success, setSuccess] = useState(false);
  const stripe = useStripe();
  const elements = useElements();
  const dados = useDadosList();
  const history = useHistory();
  const listaHook = ListaHook();
  const espetaculoUsuario = listaHook.listaEspetaculosDoUsuario;
  const plano = listaHook.listaPlanos;
  const [paymentInfo, setPaymentInfo] = useState()
  const userId = firebase.auth().currentUser.uid;

  useEffect(() => {
    const compradosArray = firebase
      .database()
      .ref("lancamentoVenda/" + firebase.auth().currentUser.uid);

    compradosArray.on("value", (snapshot) => {
      if (snapshot.val() !== null) {
        setCompradosArrayState(snapshot.val());
      }
    });

    const assinaturaArray = firebase
    .database()
    .ref("assinaturas/" + firebase.auth().currentUser.uid);
    assinaturaArray.on("value", (snapshot) => {
    if (snapshot.val() !== null) {
      console.log(snapshot.val())
      // const data = snapshot.val();
      setAssinaturaArrayState(snapshot.val());
    }})  

  },[]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
    });

    if (!error) {
      try {
        const { id } = paymentMethod;
        const response = await axios.post(
          "https://stripe-md.herokuapp.com/payment",
          {
            amount: props.amount.replace(".", "").replace(",", ""),
            id,
          }
        ).then((response) => {
          setPaymentInfo(response)
        });
        console.log(response);
        if (response.data.success) {
          async function payment() {
            let name = firebase
              ?.database()
              .ref("users/" + firebase.auth()?.currenUser?.uid + "/fullName");

            name.on("value", (snapshot) => {
              if (snapshot.val() !== null) {
                setNameComprador(snapshot.val());
              }
            });

            const data = new Date().toLocaleString("en-US", {
              timeZone: "America/Sao_Paulo",
            });

            if (props.type === "peca") {
              if (!compradosArrayState) {
                firebase
                  .database()
                  .ref("lancamentoVenda/" + userId)
                  .set([
                    {
                      userId,
                      data,
                      pe√ßa: listaHook?.listaEspetaculosDoUsuario,
                      valor: espetaculoUsuario.valorPreco,
                      name: nameComprador,
                      namePeca: listaHook?.listaEspetaculosDoUsuario.tituloEsp,
                    },
                  ]);
              } else {
                var arrayList = compradosArrayState;
                arrayList.push({
                  userId,
                  data,
                  pe√ßa: listaHook?.listaEspetaculosDoUsuario,
                  valor: espetaculoUsuario.valorPreco,
                  name: nameComprador,
                  namePeca: listaHook?.listaEspetaculosDoUsuario.tituloEsp,
                });
                arrayList.reverse();
                firebase
                  .database()
                  .ref("lancamentoVenda/" + userId)
                  .set(arrayList);
              }
            } else {
              if (!assinaturaArrayState) {
                firebase
                  .database()
                  .ref("assinaturas/" + userId)
                  .set([
                    {
                      userId,
                      data,
                      id: plano?.id,
                      valor: parseFloat(props.amount) / 100,
                      name: nameComprador,
                      nameAssinatura: plano?.name,
                      paymentInfo
                    },
                  ]);
                firebase.database().ref('users/' + userId).update({
                  signature: true
                })
              } else {
                var arrayListAssinaturas = assinaturaArrayState;

                arrayListAssinaturas.push({
                  userId,
                  data,
                  id: plano?.id,
                  valor: parseFloat(props.amount) / 100,
                  name: nameComprador,
                  nameAssinatura: plano?.name,
                  paymentInfo,
                });

                arrayListAssinaturas.reverse();

                firebase
                  .database()
                  .ref("assinaturas/" + userId)
                  .set(arrayListAssinaturas);

                firebase.database().ref('users/' + userId).update({
                  signature: true
                })
              }
              
            }
          }
          payment();
          setSuccess(true);
          dados.setPagou(true);
          props.setCompraSuccess(true)
          // alert("Pagamento feito com sucesso");
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      alert(
        `Erro no pagamento. \nVerifique se voc√™ inseriu os dados corretamente ou se o seu cart√£o √© um cart√£o v√°lido! \nC√≥digo do erro: ${error.code}`
      );
    }
  };

  return (
    <>
      {!success ? (
        <form onSubmit={handleSubmit}>
          <center>
            <fieldset className="FormGroup">
              <div className="FormRow">
                <CardElement options={CARD_OPTIONS} />
              </div>
            </fieldset>
          </center>
          <center>
            <button type="submit" className="buttonStrip btn btn-primary">
              Pagar
            </button>
          </center>
        </form>
      ) : (
        <OverlayConfirmCompra>
          <ModalConfirmCompra>
            <h3>Compra realizada!</h3>
            <h5>Voc√™ ser√° redirecionado para<br/> a confirma√ß√£o de compra.</h5>
            <button onClick={() => history.push(`/ConfirmCompra/${props.type}`)} >Continuar</button>
          </ModalConfirmCompra>
        </OverlayConfirmCompra>
      )}
    </>
  );
}
