import React, { useState } from "react";
import { useHistory } from "react-router";
import * as S from "./styles";
import Layout from "../../layout";
import Lottie from 'react-lottie'

import green from './lottie/greenCheck.json'
function ConfirmaCupom() {

  const history = useHistory();

  const [popap] = useState(false);
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: green,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice'
    }
  }

  return (
    <>

      {!popap &&
        <Layout>
          <S.Container>
            <S.PopapDiv style={{ height: "100%", padding: '50px' }}>
              <S.PopapDiv1 style={{ height: "100%", padding: '50px' }}>
                <S.Span cor="black" size="1.8" className="mb-5 mt-5" >Tela de confirma√ß√£o de sorteio </S.Span>
                <Lottie options={defaultOptions}
                  height={'auto'}
                  width={'auto'}/>
                <S.PopapDivButton>
                  <S.PopapButton onClick={() => history.push('/')}>
                    <S.Span size="1.5">PW</S.Span>
                  </S.PopapButton>
                </S.PopapDivButton>
              </S.PopapDiv1>
            </S.PopapDiv>
          </S.Container>
        </Layout>

      }


    </>
  );
}
export default ConfirmaCupom;