import React, { useState, useEffect } from "react";
import * as S from "./styles";
import Lottie from "react-lottie";
import success from "./lottie/success.json";
import falide from "./lottie/falide.json";
import { Cupoms } from "./cupom";
import { useHistory } from "react-router-dom"; 

export default function PopupCupom(props) {
  const history = useHistory()
  const [popup, setPopup] = useState(true);
  const [cupomSelect, setCupomSelect] = useState(null);
  //lottie animations
  const defaultOptionssuccess = {
    loop: false,
    autoplay: true,
    animationData: success,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const defaultOptionsfalide = {
    loop: false,
    autoplay: true,
    animationData: falide,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  //efect para capturar cupom selecionado
  useEffect(() => {
    if (props.tipo === "espetaculo") {
      if (Cupoms && props.cupom) {
        Cupoms.map((cupom) => {
          if (cupom.cupom === props.cupom && cupom.tipo === "espetaculo") {
            setCupomSelect(cupom);
          }
          return 0
        });
      }
    }
    if (props.tipo === "assinatura") {
      if (Cupoms && props.cupom) {
        if (props.cupom === "#Parm√™NoTeatro" && props.plano?.name === "Anual") {

          Cupoms.map((cupom) => {
            if (cupom.cupom === props.cupom && cupom.tipo === "assinatura") {
              setCupomSelect(cupom);
            }
            return 0
          });

        } else {

          Cupoms.map((cupom) => {
            if (cupom.cupom === props.cupom && cupom.tipo === "assinatura" && cupom.cupom !== "#Parm√™NoTeatro") {
              setCupomSelect(cupom);
            }
            return 0
          });

        }

        
      }
    }
  }, [props]);

  return (
    <>
      {popup && props.cupom && (
        <S.PopapDiv style={{ height: "100%", padding: "50px" }}>
          <S.PopapDiv1 style={{ height: "100%", padding: "50px" }}>
            {cupomSelect ? (
              <S.Span cor="black" size="1.8" className="mb-5 mt-5">
                <h1> Cupom v√°lido</h1>
              </S.Span>
            ) : (
              <S.Span cor="black" size="1.8" className="mb-5 mt-5">
                <h1>Cupom inv√°lido </h1>
              </S.Span>
            )}
            {cupomSelect && (
              <S.Span cor="black" size="1" className="">
                <h2>{cupomSelect.cupom}</h2>
              </S.Span>
            )}
            {cupomSelect && (
              <S.Span cor="black" size="0.8" className="">
                <h3>{cupomSelect.descricao}</h3>
              </S.Span>
            )}
            {cupomSelect ? (
              <Lottie
                height={"70%"}
                width={"70%"}
                options={defaultOptionssuccess}
              ></Lottie>
            ) : (
              <Lottie
                height={"auto"}
                width={"auto"}
                options={defaultOptionsfalide}
              ></Lottie>
            )}
            <S.Span cor="black" size="0.8" className="">
              <h5>{cupomSelect?.detalhes}</h5>
            </S.Span>
            <S.PopapDivButton>
              <S.PopapButton
                onClick={() => {
                  props.retorno(cupomSelect);
                  setPopup(!popup);
                  // if (props.cupom === "#TvPalcoWeb") history.push("/espetaculos");
                }}
              >
                <S.Span size="1.5">continuar</S.Span>
              </S.PopapButton>
            </S.PopapDivButton>
          </S.PopapDiv1>
        </S.PopapDiv>
      )}
    </>
  );
}
