import React, { useState, useContext, useCallback, useEffect } from "react";
import * as S from "./styles";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { AuthContext } from "../../auth/AuthContext";
import { useHistory } from "react-router-dom";
import firebase from "firebase";
import logoPW from "../../assets/logo-pw.png";
import "./style.css";

function MeuPerfil() {
  const [mostrarPerfi, setmostrarPerfi] = useState(true);
  const { currentUser } = useContext(AuthContext);
  const [historico, setHistorico] = useState([]);
  const history = useHistory();
  const [assinaturaArrayState, setAssinaturaArrayState] = useState([])

  useEffect(() => {
    let compradosArray = firebase
      .database()
      .ref("lancamentoVenda/" + firebase.auth().currentUser.uid);
    compradosArray.on("value", (snapshot) => {
      setHistorico(snapshot.val());
    });

    const assinaturaArray = firebase
      .database()
      .ref("assinaturas/" + firebase.auth().currentUser.uid);
    assinaturaArray.on("value", (snapshot) => {
    if (snapshot.val() !== null) {
      // const data = snapshot.val();
      console.log(snapshot.val())
      setAssinaturaArrayState(snapshot.val());
    }})  
  }, []);
  if (currentUser) {
    setTimeout(function () {
      let email = firebase
        .database()
        .ref("users/" + firebase.auth().currentUser.uid + "/email");
      let birthday = firebase
        .database()
        .ref("users/" + firebase.auth().currentUser.uid + "/birthday");
      let name = firebase
        .database()
        .ref("users/" + firebase.auth().currentUser.uid + "/fullName");
      let phone = firebase
        .database()
        .ref("users/" + firebase.auth().currentUser.uid + "/phone");

      email.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          document.getElementById("email").value = snapshot.val();
        }
      });
      name.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          document.getElementById("name").value = snapshot.val();
        }
      });
      birthday.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          document.getElementById("birthday").value = snapshot.val();
        }
      });
      phone.on("value", (snapshot) => {
        if (snapshot.val() !== null) {
          document.getElementById("phone").value = snapshot.val();
        }
      });
    });
  }

  console.log(assinaturaArrayState)

  const dataUpdate = useCallback(async (event) => {
    event.preventDefault();
    try {
      let phone = document.getElementById("phone");
      let birthday = document.getElementById("birthday");
      let name = document.getElementById("name");
      let email = document.getElementById("email");
      let userId = firebase.auth().currentUser.uid;

      if (phone.value != null) {
        firebase
          .database()
          .ref("users/" + userId)
          .update({
            phone: phone.value,
          });
      }
      if (birthday.value !== null) {
        firebase
          .database()
          .ref("users/" + userId)
          .update({
            birthday: birthday.value,
          });
      }
      if (name.value !== null) {
        firebase
          .database()
          .ref("users/" + userId)
          .update({
            fullName: name.value,
          });
      }
      if (email.value !== null) {
        firebase
          .database()
          .ref("users/" + userId)
          .update({
            email: email.value,
          });
      }
    } catch (error) {
      alert(error);
    }
  }, []);

  if (!currentUser) {
    history.push("/login");
  }

  return (
    <>
      <Header />

      <S.Container>
        <S.TextoLateral>
          <S.AreaFotoPerfil>
            <S.Foto className="m-2 ">
            {firebase.auth().currentUser.photoURL === null ? <i style={{transform: "scale(3)"}} className="bi bi-person-fill"></i> : <img alt="" style={{ width: '50px', height: '50px', borderRadius: "15px" }} src={firebase.auth().currentUser.photoURL}></img>}
            </S.Foto>
          </S.AreaFotoPerfil>
          <S.SpanLatera size="15" cor="#FFF" className="m-3" setadoPerfil={mostrarPerfi} onClick={() => setmostrarPerfi(true)}> Dados Pessoais</S.SpanLatera>
          <S.SpanCompra size="15" cor="#FFF" className="m-3" setadoCompra={mostrarPerfi} onClick={() => setmostrarPerfi(false)}>Relatorio de compras</S.SpanCompra> 
        </S.TextoLateral>

        {mostrarPerfi ? (
          <>
            
            <S.AreaPerfil>
                <S.SpanInput size="23" className=" p-5 ">
                  Minha conta
                </S.SpanInput>
                <S.AreaFotoPerfil1>
                  <S.Foto className="m-2 ">
                    {firebase.auth().currentUser.photoURL === null ? <i style={{transform: "scale(3)"}} className="bi bi-person-fill"></i> : <img alt="" style={{ width: '50px', height: '50px', borderRadius: "15px" }} src={firebase.auth().currentUser.photoURL}></img>}
                  </S.Foto>
                </S.AreaFotoPerfil1>
              <form onSubmit={() => dataUpdate}>
                
                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Nome Completo
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    id="name"
                    name="name"
                    minLength="4"
                    maxLength="20"
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Data de nascimento
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    type="date"
                    id="birthday"
                    name="birthday"
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Celular
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    id="phone"
                    name="phone"
                    minLength="11"
                    maxLength="11"
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    E-mail
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    type="text"
                    name="email"
                    id="email"
                  ></input>
                </S.GrupoInput>

                {/* <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2"> Senha</S.SpanInput>
                  <S.Input disabled type="password" value="123" className="input"></S.Input>

                </S.GrupoInput> */}
              </form>
            </S.AreaPerfil>
          </>
        ) : (
          //relatorio de compras
          <S.AreaCompras>
            <S.SpanInput size="23" className=" p-5 ">
              Minha conta
            </S.SpanInput>
            <S.Carrinho>
              
              <S.Info>
                {assinaturaArrayState[0]
                ? assinaturaArrayState.map((plan) => {
                  var plainValue = plan?.valor.toString().replace(".", ",")
                  return (
                    <>
                      <div
                        style={{
                          display: "flex",
                          height: "150px",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <S.ImgPlano
                          style={{ float: "left" }}
                          src={logoPW}
                        ></S.ImgPlano>
                        <div
                          style={{
                            display: "flex",
                            flexDirection: "column",
                            marginLeft: "30px",
                          }}
                        >
                          <S.Span size="15">
                            Plano:{" "}
                            <S.Span cor="#FDCC12" size="15">
                              {plan?.nameAssinatura}
                            </S.Span>
                          </S.Span>
                          <S.Span size="15">
                            Data da compra:{" "}
                            {plan?.data
                              .split("/")[1]
                              .concat("/" + plan?.data.split("/")[0])
                              .concat("/" + plan?.data.split("/")[2])}
                          </S.Span>
                          <S.Span size="15">
                            Valor do plano: {plainValue}
                          </S.Span>
                          <S.Span size="15">
                            <S.Button>Extornar</S.Button>
                          </S.Span>
                        </div>
                      </div>
                      <S.BordarBottom></S.BordarBottom>
                      <br />
                    </>
                  );
                }) : <h1 style={{margin: "auto"}}>Nenhuma assinatura ativa</h1>
                }
                {historico
                  ? historico.map((array) => {
                      return (
                        <div>
                          
                        <div style={{display: "flex", height: "150px", alignItems: "center", width: "100%"}}>
                          <S.Img src={array.pe√ßa.thumbnail}></S.Img>
                          <div style={{display: "flex", flexDirection: "column", marginLeft: "30px"}}>
                          <S.Span  size="15">
                            Titulo: <S.Span cor="#FDCC12" size="15">
                              {array.namePeca}
                            </S.Span>
                            
                          </S.Span>
                          <S.Span style={{display: "flex"}} size="15">
                            Produ√ß√£o: {array.pe√ßa.producao}
                          </S.Span>
                          <S.Span size="15">
                            Data da compra: {array.data.split('/')[1].concat('/'+array.data.split('/')[0]).concat('/'+array.data.split('/')[2])}
                          </S.Span>
                          <S.Span size="15">
                            Valor da pe√ßa: {array.valor}
                          </S.Span>
                          
                          </div>

                          {/* <S.Span size="15">agendamento: 19/08/2021 21:17</S.Span> */}
                          {/* <S.Span size="15 ">BackStage: Sim</S.Span> */}
                        </div>
                        <S.BordarBottom></S.BordarBottom>
                        <br />

                        </div>
                      );
                    })
                  : <h1 style={{margin: "auto"}}>Nenhuma compra realizada</h1>}
                {/* <S.Span size="15">
                  Produtora:{" "}
                  <S.Span cor="#FDCC12" size="15">
                    {" "}
                    Chain Entreteriments
                  </S.Span>
                </S.Span>
                <S.Span size="15">data compra: 19/08/2021 21:17</S.Span>
                <S.Span size="15">agendamento: 19/08/2021 21:17</S.Span>
                <S.Span size="15 ">BackStage: Sim</S.Span>
              */}{" "}
              </S.Info>

              <S.CarrinhoItem>
                {/* <S.Span size="12">
                  Total pago:{" "}
                  <S.Span cor="#FDCC12" size="12">
                    R$ 35
                  </S.Span>{" "}
                </S.Span> */}
              </S.CarrinhoItem>
            </S.Carrinho>
          </S.AreaCompras>
        )}
      </S.Container>
      <Footer />
    </>
  );
}

export default MeuPerfil;
