import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import * as S from "./styles";
import { useHistory } from "react-router-dom";
import Share from "../../components/CardCompartilhar/index";
import firebase from "firebase";
import axios from "axios";

export default function ComponenteVideo() {
  const history = useHistory();

  // OBS -->>>> Comentei a fun√ß√£o da visualiza√ß√£o e o state dela pra tirar os warnings do c√≥digo
  // Favor quando deixar uma fun√ß√£o incompleta, comentar
  // const [visualicacao, setvisualicacao] = useState(0);

  const [compartilhar, setcompartilhar] = useState(false);
  const params = useParams("/playespetaculo/:id/:id2/:id3");
  const { id, id2, id3 } = params;
  const [um, setUm] = useState(null);
  const [dois, setDois] = useState(null);
  const [tres, setTres] = useState(null);
  const [quatro, setQuatro] = useState(null);
  const [cinco, setCinco] = useState(null);
  const [umHover, setUmHover] = useState(null);
  const [doisHover, setDoisHover] = useState(null);
  const [tresHover, setTresHover] = useState(null);
  const [quatroHover, setQuatroHover] = useState(null);
  const [cincoHover, setCincoHover] = useState(null);
  const [validacao, setValidacao] = useState(null);
  const [avaliacaoTotal, setAvaliacaoTotal] = useState(null);

  setTimeout(function () {
    let currentTotal = firebase
      .database()
      .ref("video/" + id + "/totalAvaliacoes");
    let currentAvaliacao = firebase.database().ref("video/" + id + "/nota");

    currentTotal.on("value", (snapshot) => {
      setAvaliacaoTotal(snapshot.val());
    });

    currentAvaliacao.on("value", (snapshot) => {
      setValidacao(snapshot.val());
    });
  });

 

  const sendAvalliacao = (total) => {
    axios.post("https://json-serverepw.herokuapp.com/lista?videoID=" + id, {
      nota: validacao ? validacao + total : total,
      pecaId: id,
      totalAvaliacoes: avaliacaoTotal ? avaliacaoTotal + 1 : 1,
      mediaNota:
        validacao && avaliacaoTotal
          ? (validacao + total) / (avaliacaoTotal + 1)
          : total,
    });

    // firebase.database().ref('video/' + id).update({
    //   nota: validacao ? validacao + total : total,
    //   pecaId: id,
    //   totalAvaliacoes: avaliacaoTotal ? avaliacaoTotal + 1 : 1,
    //   mediaNota: validacao && avaliacaoTotal ? ((validacao + total)  / (avaliacaoTotal + 1)) : total
    // })
  };

  function feedback(um, dois, tres, quatro, cinco) {
    if (um) {
      setUm(true);
      setDois(false);
      setTres(false);
      setQuatro(false);
      setCinco(false);
      sendAvalliacao(um);
    }

    if (dois) {
      setUm(true);
      setDois(true);
      setTres(false);
      setQuatro(false);
      setCinco(false);
      sendAvalliacao(dois);
    }
    if (tres) {
      setUm(true);
      setDois(true);
      setTres(true);
      setQuatro(false);
      setCinco(false);
      sendAvalliacao(tres);
    }
    if (quatro) {
      setUm(true);
      setDois(true);
      setTres(true);
      setQuatro(true);
      setCinco(false);
      sendAvalliacao(quatro);
    }

    if (cinco) {
      setUm(true);
      setDois(true);
      setTres(true);
      setQuatro(true);
      setCinco(true);
      sendAvalliacao(cinco);
    }
  }

  useEffect(() => {
    let viewsCounter = firebase.database().ref("video/" + id + "/views");
    viewsCounter.on("value", (snapshot) => {
      setTimeout(() => {
        firebase
          .database()
          .ref("video/" + id)
          .set({
            views: snapshot.val() + 1,
          })
          .then(() => {
            console.log(":)");
          })
          .catch(() => {
            console.log(":(");
          });
      }, 960000);
    });
  }, [id]);

  return (
    <>
      <div style={{ width: "100vw", display: "flex", padding: "0px" }}>
        <S.div>
          <S.Span
            size="50"
            style={{ cursor: "pointer" }}
            onClick={() => history.push("/")}
            hover={"#FDCC12"}
          >
            <i className="bi bi-arrow-left bold"></i>
          </S.Span>
          <S.Span
            size="30"
            style={{ cursor: "pointer" }}
            className="bold"
            hover={"#FDCC12"}
          >
            <i
              onClick={() =>
                compartilhar ? setcompartilhar(false) : setcompartilhar(true)
              }
              className="bi bi-share bold"
            ></i>{" "}
            {compartilhar && (
              <Share link={`https://palcoweb.com.br/#/cardezao/${id3}`}></Share>
            )}
          </S.Span>

          <div>
            <S.Span size="22" className="m-2 bold">
              Gostou? Avalie:
            </S.Span>
            <S.Estrelinhas
              seleacionado={um ? "#FDCC12" : false}
              seleacionadoHover={umHover ? "#FDCC12" : false}
              onClick={() => feedback(20, null, null, null)}
              onMouseEnter={() => setUmHover(true)}
              onMouseOut={() => setUmHover(false)}
            >
              <i className="bi bi-star-fill"></i>
            </S.Estrelinhas>

            <S.Estrelinhas
              seleacionado={dois ? "#FDCC12" : false}
              seleacionadoHover={doisHover ? "#FDCC12" : false}
              onClick={() => feedback(null, 40, null, null)}
              onMouseEnter={() => {
                setUmHover(true);
                setDoisHover(true);
              }}
              onMouseOut={() => {
                setUmHover(false);
                setDoisHover(false);
              }}
            >
              <i className="bi bi-star-fill"></i>
            </S.Estrelinhas>

            <S.Estrelinhas
              seleacionado={tres ? "#FDCC12" : false}
              seleacionadoHover={tresHover ? "#FDCC12" : false}
              onClick={() => feedback(null, null, 60, null)}
              onMouseEnter={() => {
                setUmHover(true);
                setDoisHover(true);
                setTresHover(true);
              }}
              onMouseOut={() => {
                setUmHover(false);
                setDoisHover(false);
                setTresHover(false);
              }}
            >
              <i className="bi bi-star-fill"></i>
            </S.Estrelinhas>
            <S.Estrelinhas
              seleacionado={quatro ? "#FDCC12" : false}
              seleacionadoHover={quatroHover ? "#FDCC12" : false}
              onClick={() => feedback(null, null, null, 80)}
              onMouseEnter={() => {
                setUmHover(true);
                setDoisHover(true);
                setTresHover(true);
                setQuatroHover(true);
              }}
              onMouseOut={() => {
                setUmHover(false);
                setDoisHover(false);
                setTresHover(false);
                setQuatroHover(false);
              }}
            >
              <i className="bi bi-star-fill"></i>
            </S.Estrelinhas>
            <S.Estrelinhas
              seleacionado={cinco ? "#FDCC12" : false}
              seleacionadoHover={cincoHover ? "#FDCC12" : false}
              onClick={() => feedback(null, null, null, null, 100)}
              onMouseEnter={() => {
                setUmHover(true);
                setDoisHover(true);
                setTresHover(true);
                setQuatroHover(true);
                setCincoHover(true);
              }}
              onMouseOut={() => {
                setUmHover(false);
                setDoisHover(false);
                setTresHover(false);
                setQuatroHover(false);
                setCincoHover(false);
              }}
            >
              <i className="bi bi-star-fill"></i>
            </S.Estrelinhas>
          </div>
        </S.div>

        <iframe
          id="vimeoFrame"
          src={`https://player.vimeo.com/video/${id}?h=${id2}`}
          frameBorder="0"
          allow="autoplay; fullscreen; picture-in-picture"
          title="Cad&amp;ecirc;.mp4"
          style={{
            position: "absolute",
            marginTop: "0px",
            padding: 0,
            width: "100vw",
            height: "100vh",
          }}
        ></iframe>
      </div>
    </>
  );
}
