import styled from "styled-components";
import InputMask from "react-input-mask";
import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.div`
  width: 50vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: calc(100vh - 90px);
  color: white;
  filter: ${(props) => props.paymentTrue ? 'blur(5px)' : 'blur(0)'}

`;

export const CampForm = styled.form`
  display: flex;
  flex-direction: none;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 400px;
  margin: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 5px 5px 5px 5px rgba(127, 255, 212, 0.1);
`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 1rem;
  font-size: ${(props) => props.size + "rem"};
`;

export const InputCardNumber = styled.input`
  width: 40vw;
  height: 5vh;
  margin-top: 0;
  padding-left: 30px;
  background-color: white;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.3);
  input-placeholder: black;
  font-weight: 500;
  fontsize: 16px;
  border-radius: 4px;
  &::-webkit-input-placeholder {
    color: #aaa;
  }
  @media (max-width: ${SCREEN_SIZES.xs}) {
    width: 90vw !important;
  }
`;
export const InputTitular = styled.input`
  width: 80%;
  height: 50px;
  background-color: white;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.3);
`;
export const CampoTexto = styled.div`
  width: 80%;
`;

export const CampoData = styled.div`
  width: 80%;
`;
export const Select = styled.select`
  width: 20%;
  height: 40px;
  margin: 5px;
  padding: 5px;
`;
export const Input = styled(InputMask)`
  width: ${(props) => (props.size ? props.size + "px" : "100%")};
  margin: 0 !important;
`;

export const Button = styled.button`
  background-color: #fdcc12;
  color: #1d1d1d;
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 !important;
  border: none;
  border-radius: 5px;
  width: 150px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3ms;
  :hover {
    background-color: #fdcc10;
    color: #000;
  }
  :disabled {
    background-color: gray;
    color: #fff;
    cursor: default;
  }
`;

export const ButtonVoltar = styled.button`
  text-transform: uppercase;
  font-size: 1rem;
  border: none;
  padding: 10px;
  color: #fff !important;
  transition: all 0.3s;
  background: transparent !important;
  &:hover {
    color: #d7ac02;
  }
`;

export const Form = styled.form`
  width: 100%;
  max-width: 600px;
  min-width: 500px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
`;

export const Detalhes = styled.div`
  width: 100%;
  max-width: 600px;
  min-width: 500px;
  gap: 30px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0px 20px;
  margin-bottom: 30px;
`;

export const DetalhesContainer = styled.div`
  width: 100%;
  gap: 10px;
  background: #1b1b1b;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
`;

export const DetalhesRow = styled.div`
  width: ${(props) => (props.size ? props.size + "px" : "100%")};
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
`;

export const Row = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0px;
`;

export const ContainerActions = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
`;

export const Doisinupt = styled.div`
  display: flex;
  margin-left: 9%;
`;

export const Img = styled.img`
  width: 125px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  object-fit: cover;
  justify-content: center;
  height: 220px;
`

export const OverlayConfirmCompra = styled.div`
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba($color: #000, $alpha: 0.2);
  display: flex;
  justify-content: center;
  align-items: center;

`;

export const ModalConfirmCompra = styled.div`
  width: 25vw;
  min-height: 30vh;
  background: #d7ac02;
  opacity: 1;
  border-radius: 15px;
  padding: 20px;
  color: black;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width:768px) {
    width: 90vw !important;	     
  }
  
  button {
    background-color:#222;
    font-size: 3vh;
    color: #fff;
    border-radius: 8px;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    &:hover{
      background: #363636;
    }
  
  } 
`;

export const ImgPlano = styled.img`
  width: 250px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  object-fit: cover;
  justify-content: center;
  height: 220px;
  border-radius: 10px;
`

export const Div = styled.div`
  width: 40vw;
  margin-bottom: 2vh;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width:768px) {
    width: 90vw !important;	     
  }
`

export const TelaVazia = styled.span`
  width: 100vw;
  color:white;
  font-size:1.5vw;
  display: flex;
  flex-direction: column;

  @media (max-width:768px) {
    font-size:4.5vw;
  }
`